<template>
  <div>
    <canvas
      ref="canvasRef"
      :width="sizeRef"
      :height="sizeRef"
      @mousedown="startSelection"
      @mousemove="moveSelection"
      @mouseup="endSelection"
    ></canvas>
  </div>
  <div class="actions">
    <app-button
      severity="secondary"
      raised
      :icon="['fas', 'undo']"
      @click="resetImage"
    ></app-button>
    <app-button severity="secondary" raised :icon="['fas', 'plus']" @click="zoomPlus"></app-button>
    <app-button
      severity="secondary"
      raised
      :icon="['fas', 'minus']"
      @click="zoomMinus"
    ></app-button>
    <app-button severity="secondary" raised :icon="['fas', 'save']" @click="saveImage"></app-button>
  </div>
</template>

<script lang="ts">
import { defineComponent, nextTick, onMounted, ref, watch } from 'vue';
import { FrameColorSegmentType } from '@/types/frame-color-segment.type';
import AppButton from '@/shared/components/AppButton.vue';
import { useCanvasSaver } from '@/composables/useCanvasSaver';
import { useStore } from 'vuex';

export default defineComponent({
  name: 'AppEditorCanvas',
  components: { AppButton },
  props: {
    segments: { type: Array as () => FrameColorSegmentType[], required: true },
    imageUrl: { type: [String, null], default: null },
  },
  emits: ['update:imageUrl'],
  setup(props, { emit }) {
    const store = useStore();
    const canvasRef = ref<HTMLCanvasElement | null>(null);
    const imageRef = ref<HTMLImageElement | null>(null);
    const originalUrlRef = ref<string | null>(null);
    const thicknessRef = ref(90);
    const sizeRef = ref(480);
    const rotationRef = ref(0); //  
    const startAngleRef = ref<number | null>(null); //   , ,      12 
    const offsetXRef = ref(0); //     X
    const offsetYRef = ref(0); //     Y
    // --- Crop selection ---
    let isSelecting = false;
    let startX = 0,
      startY = 0,
      currentX = 0,
      currentY = 0;
    let isLoadImage = false;
    const render = async () => {
      if (!canvasRef.value) return;
      const ctx = canvasRef.value.getContext('2d')!;
      ctx.clearRect(0, 0, sizeRef.value, sizeRef.value);
      if (props.imageUrl && !isLoadImage) {
        imageRef.value = await loadImage(props.imageUrl);
        isLoadImage = true;
      }
      drawBaseImage(ctx);
      drawFrame(ctx);
    };

    const { saveImage, loadImage, resetImage, zoomPlus, zoomMinus, zoom } = useCanvasSaver(
      canvasRef,
      render,
      emit,
      originalUrlRef
    );

    const startSelection = (e: MouseEvent) => {
      if (!canvasRef.value) return;
      isSelecting = true;
      const rect = canvasRef.value.getBoundingClientRect();
      startX = e.clientX - rect.left;
      startY = e.clientY - rect.top;
      currentX = startX;
      currentY = startY;
      draw();
    };

    const moveSelection = (e: MouseEvent) => {
      if (!isSelecting || !canvasRef.value) return;
      const rect = canvasRef.value.getBoundingClientRect();
      currentX = e.clientX - rect.left;
      currentY = e.clientY - rect.top;
      draw();
    };

    const endSelection = () => {
      if (!canvasRef.value) return;
      isSelecting = false;
      draw();

      const x = Math.min(startX, currentX);
      const y = Math.min(startY, currentY);
      const w = Math.abs(currentX - startX);
      const h = Math.abs(currentY - startY);

      if (w < 2 || h < 2) return;

      const cropCanvas = document.createElement('canvas');
      cropCanvas.width = w;
      cropCanvas.height = h;
      const cropCtx = cropCanvas.getContext('2d')!;

      cropCtx.drawImage(canvasRef.value, x, y, w, h, 0, 0, w, h);

      const croppedUrl = cropCanvas.toDataURL('image/png');
      emit('update:imageUrl', croppedUrl);
    };

    const draw = () => {
      if (!canvasRef.value) return;
      const ctx = canvasRef.value.getContext('2d')!;
      ctx.clearRect(0, 0, sizeRef.value, sizeRef.value);
      drawBaseImage(ctx);
      drawFrame(ctx);

      if (isSelecting) {
        ctx.strokeStyle = 'red';
        ctx.lineWidth = 2;
        ctx.strokeRect(startX, startY, currentX - startX, currentY - startY);
      }
    };

    const drawBaseImage = (ctx: CanvasRenderingContext2D) => {
      const radius = sizeRef.value / 2; //   ,    
      const thickness = thicknessRef.value; //   (  )
      const img = imageRef.value;
      if (!img) return;

      ctx.save(); //    
      ctx.beginPath(); //      
      //         ,   
      ctx.arc(radius, radius, radius - thickness, 0, 2 * Math.PI);
      ctx.closePath(); //  
      ctx.clip(); //     (    )
      //       +  
      ctx.translate(radius + (offsetXRef.value ?? 0), radius + (offsetYRef.value ?? 0));
      ctx.rotate((rotationRef.value * Math.PI) / 180); //      (   )

      const baseSize = sizeRef.value - thickness * 2; //     (   )
      const minSide = Math.min(img.width, img.height); //     (   )
      const targetSize = baseSize * zoom.value; //     
      //  :
      // -        minSide  minSide
      // -     
      // -   targetSize  targetSize
      ctx.drawImage(
        img,
        (img.width - minSide) / 2, // x- 
        (img.height - minSide) / 2, // y- 
        minSide, //  
        minSide, //  
        -targetSize / 2, // x-  
        -targetSize / 2, // y-  
        targetSize, //   
        targetSize //   
      );
      //    
      ctx.restore();
    };

    const drawFrame = (ctx: CanvasRenderingContext2D) => {
      const radius = sizeRef.value / 2; //    (  )
      const thickness = thicknessRef.value; //  ,      
      const step = (2 * Math.PI) / props.segments.length; //    ( ),  2 /  
      //    (      ,   -/2)
      const startOffset = startAngleRef.value ?? -Math.PI / 2;

      ctx.lineWidth = thickness; //      
      //        
      props.segments.forEach((seg, i) => {
        ctx.beginPath(); //      
        ctx.strokeStyle = seg.color; //      
        const start = i * step + startOffset; //       i
        const end = (i + 1) * step + startOffset; //       i
        //         
        //  : (radius, radius)
        // : radius - thickness / 2 (     )
        ctx.arc(radius, radius, radius - thickness / 2, start, end);
        ctx.stroke(); //    
      });
    };

    onMounted(render);
    watch(
      () => props.imageUrl,
      (url) => {
        if (url && !originalUrlRef.value) {
          originalUrlRef.value = url;
        }

        if (url && originalUrlRef.value) {
          isLoadImage = false;
          zoom.value = 1;
          originalUrlRef.value = url;
        }

        render();
      }
    );

    watch(
      () => store.getters['mobile/clientWidth'],
      (value) => {
        if (value < 600) {
          thicknessRef.value = 40;
          sizeRef.value = 280;
          nextTick(() => render());
          return;
        }

        if (value < 1024) {
          thicknessRef.value = 70;
          sizeRef.value = 400;
          nextTick(() => render());
          return;
        }

        if (value > 1024) {
          thicknessRef.value = 90;
          sizeRef.value = 480;
          nextTick(() => render());
          return;
        }
      },
      { immediate: true }
    );

    watch(
      () => props.segments,
      () => {
        nextTick(() => render());
      }
    );

    return {
      canvasRef,
      startSelection,
      moveSelection,
      endSelection,
      resetImage,
      saveImage,
      zoomPlus,
      zoomMinus,
      sizeRef,
    };
  },
});
</script>
<style scoped>
.actions {
  display: flex;
  gap: 12px;
  margin-top: 10px;
}
</style>
